import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { IDiscount, IDiscountAdd, IDiscountRes } from 'src/app/shared/interfaces/i-discount';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class OfferService {
  constructor(private http: HttpClient) { }

  showDiscounts(): Observable<IDiscountRes>{
    return this.http.get<IDiscountRes>(`${environment.API_URL}/v1/service-discounts`);
  }

  createDiscount(data: IDiscount): Observable<IDiscountAdd> {
    return this.http.post<IDiscountAdd>(`${environment.API_URL}/v1/service-discounts`,data);
  }

  editDiscount(data: IDiscount): Observable<IDiscountAdd>{
    return this.http.patch<IDiscountAdd>(`${environment.API_URL}/v1/service-discounts/${data.id}`,data);
  }
}
