import { DatePipe } from '@angular/common';
import { Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { IDiscount } from 'src/app/shared/interfaces/i-discount';
import { IServiceStore } from 'src/app/shared/interfaces/i-service';
import { NotificationService } from '../../../services/notification.service';
import { ServiceService } from '../../service/services/service.service';
import { OfferService } from '../services/offer.service';

@Component({
  selector: 'app-offers-dialog',
  templateUrl: './offers-dialog.component.html',
  styleUrls: ['./offers-dialog.component.scss']
})
export class OffersDialogComponent implements OnInit {

  servicesStore!: IServiceStore[];
  offerForm!: FormGroup;
  offerData!: IDiscount;
  constructor(
    public dialogRef: MatDialogRef<OffersDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    private discountSvc: OfferService,
    private serviceSvc: ServiceService,
    private formB: FormBuilder,
    private notiSvc: NotificationService,
    public datepipe:    DatePipe) { 
      this.buildForm()
    }

  ngOnInit(): void {
    this.getServiceStore()
    if (this.data?.element){
      this.offerForm.patchValue(this.data.element)
    }
  }

  getServiceStore(): void {
    this.serviceSvc.showServicesStore().subscribe((data) => {
      this.servicesStore = data.data
    })
  }

  submitOffer(): void {
    if(this.offerForm.invalid){
      this.notiSvc.openSnackBar("Algunos de los campos son invalidos", 3000)
      return
    }
    this.setOffer()
    if(this.data?.element){
      this.discountSvc.editDiscount(this.offerData).subscribe(
        val => {
          console.log(val)
          this.notiSvc.openSnackBar("Promocion actualizada correctamente", 2000)
          this.dialogRef.close()
        },
        error => {
          console.log(error)
          this.notiSvc.openSnackBar("Hubo un error al actualizar la promocion",2000)
        }
      )
    }else {
      this.discountSvc.createDiscount(this.offerData).subscribe(
        val => {
          console.log(val)
          this.notiSvc.openSnackBar("Promocion agregada correctamente", 2000)
          this.dialogRef.close()
        },
        error => {
          console.log(error)
          this.notiSvc.openSnackBar("Hubo un error al agregar la promocion",2000)
        }
      )
    }
  }

  buildForm(): void {
    this.offerForm = this.formB.group({
      percentage_discount: ['', Validators.required],
      start_date: ['', Validators.required],
      end_date: ['', Validators.required],
      store_services_id: ['', Validators.required]
    })
  }

  setOffer(): void {
    this.offerData = {
      percentage_discount: this.offerForm.get('percentage_discount')?.value,
      start_date: this.datepipe.transform(this.offerForm.get('start_date')?.value, 'yyyy/MM/dd'),
      end_date: this.datepipe.transform(this.offerForm.get('end_date')?.value, 'yyyy/MM/dd'),
      store_services_id: this.offerForm.get('store_services_id')?.value,
      id: this.data?.element ? this.data?.element.id : null
    }
  }
}
