import { animate, state, style, transition, trigger } from '@angular/animations';
import { DatePipe, formatDate } from '@angular/common';
import { Component, OnInit, ViewChild } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { IDiscount } from 'src/app/shared/interfaces/i-discount';
import { IServiceStore } from 'src/app/shared/interfaces/i-service';
import { ServiceService } from '../service/services/service.service';
import { OffersDialogComponent } from './offers-dialog/offers-dialog.component';
import { OfferService } from './services/offer.service';

@Component({
  selector: 'app-offers',
  templateUrl: './offers.component.html',
  styleUrls: ['./offers.component.scss'],
  animations: [
    trigger('detailExpand', [
      state('collapsed', style({height: '0px', minHeight: '0'})),
      state('expanded', style({height: '*'})),
      transition('expanded <=> collapsed', animate('225ms cubic-bezier(0.4, 0.0, 0.2, 1)')),
    ]),
  ],
})
export class OffersComponent implements OnInit {
  @ViewChild(MatPaginator) paginator!: MatPaginator;
  @ViewChild(MatSort) sort!: MatSort;
  displayedColumns: string[] = ['id', 'store_name', 'service_name', 'price_discount', 'end_date', 'see', 'edit'];
  dataSource!: MatTableDataSource<any>;
  serviceStore!: IServiceStore[];
  discounts: IDiscount[] = [];
  today = new Date();
  jstoday = '';
  constructor(private serviceSvc: ServiceService, 
              private discountSvc: OfferService,
              public dialog: MatDialog,
              public datepipe: DatePipe) {
  }

  ngOnInit(): void {
    this.getDiscounts()
    this.getServices()
    this.jstoday = formatDate(this.today, 'yyyy/MM/dd', 'en-US');
  }

  getDiscounts(): void{
    this.discountSvc.showDiscounts().subscribe((data) => {
      this.discounts = data.data
      this.setData()
    })
  }

  openDialog(edit: boolean, element?: IDiscount): void {
    const dialogRef = this.dialog.open(OffersDialogComponent, {
      disableClose: false,
      data: {edit, element}
    }).afterClosed().subscribe((data: any) => {
      if(!data){
        this.getDiscounts();
      }
    });
  }

  setData(): void {
    this.dataSource = new MatTableDataSource();
    this.dataSource.data = this.discounts;
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
  }

  applyFilter(event: Event) {
      const filterValue = (event.target as HTMLInputElement).value;
      this.dataSource.filter = filterValue.trim().toLowerCase();

      if (this.dataSource.paginator) {
        this.dataSource.paginator.firstPage();
      }
    }

    getServices(): void{
      this.serviceSvc.showServicesStore().subscribe((data) => {
        this.serviceStore = data.data
      })
    }

    getExpiration(element: IDiscount): boolean{
      return this.datepipe.transform(element.end_date, 'yyyy/MM/dd') < this.jstoday
    }
}
